<template>
	<view>
		<navbar :iSimmersive="true" title-color="#000" :placeholder="false" title="我的推广"></navbar>
		<view class="header">
			<view class="bg">
				<view class="a-1">当前佣金</view>
				<view class="num">{{amount||'0.00'}}</view>
				<view class="b-1">
				   <view>
					   <text>昨日收益</text>
					   <text>{{yesterday_amount||'0.00'}}</text>
				   </view>
					<view>
						<text>累积已提</text>
						<text>{{amount_count||'0.00'}}</text>
					</view>
				</view>
				<view class="c-1" v-if="switch_1">
					<text @click="go('spread/cashrecord')">提现记录 &gt;</text>
					<text @click="go('spread/settlement')">立即提现 &gt;</text>
				</view>
			</view>
		</view>
		
		<view class="list">
			<view class="item">
				<view class="item-box" @click="go('spread/poster')">
					<view class="icon">
						<text class="iconfont">&#xe66b;</text>
					</view>
					<view class="text">推广名片</view>
				</view>
			</view>

			<view class="item">
				<view class="item-box" @click="go('spread/promotion_list')">
					<view class="icon">
						<text class="iconfont">&#xe65f;</text>
					</view>
					<view class="text">推广统计</view>
				</view>
			</view>

			<view class="item">
				<view class="item-box" @click="go('spread/promotion_order')">
