		</view>
		
	</view>
</template>

<script>
	import navbar from "@/components/navbar/navbar";
	export default {
		components: {
			navbar
		},
		data() {
			return {
				amount: "0.00",
				amount_count: 0,
				switch_1: 0,
				yesterday_amount: 0,
			};
		},
		onLoad() {
			this.$http.getSpreadIndex().then(res=>{
				if(res.status){
					this.amount = res.data.amount;
					this.amount_count = res.data.amount_count;
					this.yesterday_amount = res.data.yesterday_amount;
					// #ifdef MP
					this.switch_1 = res.data.switch_1;
					// #endif
					
					// #ifdef H5 || APP-PLUS
					this.switch_1 = 1;
					// #endif
				}
			});
		},
		methods: {
			go(value,param){
			    let params = param || "";
			    if(params == ""){
					this.$utils.navigateTo(value);
			    }else{
					this.$utils.navigateTo(value,params);
			    }
			}
		}
	}
</script>

